.feature__pic {
  white-space: nowrap; }
  .feature__pic:before {
    display: inline-block;
    content: '';
    height: 100%;
    vertical-align: middle;
    width: 0; }
  .feature__img {
    display: inline-block;
    max-width: 100%;
    vertical-align: middle;
    white-space: normal; }

.feature__name {
  display: block;
  position: relative;
  overflow: hidden; }
  .feature__name:after {
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #fff 80%, #fff 100%);
    bottom: 0;
    right: 0;
    content: "";
    height: 23px;
    position: absolute;
    top: 0;
    width: 10%; }

.feature {
  text-align: center; }
  .feature__name {
    color: #676d72;
    font-size: 1em;
    height: 69.6px;
    margin: 0 14% 23.2px; }
    .feature__name:after {
      top: 46.4px; }
  .feature__icon, .feature__pic {
    display: block;
    margin-bottom: 5.75px; }
  .feature__icon {
    font-size: 66px;
    fill: #000; }
    .feature__icon > svg {
      display: block;
      height: 1em !important;
      margin: auto;
      width: 1em !important; }
  .feature > a:hover {
    text-decoration: none; }
